html {
	width: 100%;
	height: 100%;
}

body {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

#root {
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
}

.layout {
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.question {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 8px;
	background-color: lightblue;
}

.choices {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 4px;
}

.questionNumber {
	font-size: 16px;
	margin: 4px;
}

.choice {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	margin: 2px;
}


.solutions {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin: 8px;
	margin-left: 32px;
}

.solutionsTitle {
	font-size: 16px;
}

.solution {
	margin: 2px;
	font-size: 14px;
}

